import React from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Home from "./routes/Home";
import UserSignUp from "./routes/UserSignUp";
import UserSignIn from "./routes/UserSignIn";
import Header from "./components/Header";
import Footer from "./components/Footer";
import CategoryAge from "./routes/CategoryAge";

const AppRouter = () => {
  return (
    <BrowserRouter>
      <Header />
      <Routes>
        <Route exact path="/react-toytoy-platform/" element={<Home />}></Route>
        <Route
          exact
          path="/react-toytoy-platform/age"
          element={<CategoryAge />}
        ></Route>
        <Route
          exact
          path="/react-toytoy-platform/users/signup"
          element={<UserSignUp />}
        ></Route>
        <Route
          exact
          path="/react-toytoy-platform/users/login"
          element={<UserSignIn />}
        ></Route>
      </Routes>
      <Footer />
    </BrowserRouter>
  );
};

export default AppRouter;
